}

.conversation-container>div::-webkit-scrollbar-thumb
{
	border-radius: 10px;
    background-color: blueviolet;


}.chat-user-box  {
    position: relative;
    margin-bottom: 30px;
    cursor: pointer;
   
}
.chat-user-box img{

    width:37px;
    height: 37px;
    border-radius: 50%;
}
.chat-user-box .message-info {
position: absolute;
padding-left: 40px;
top: 0;
overflow: hidden;
height: 50px;
width: calc(100%) ;
 border-bottom: 1px solid lightgray
}

.chat-user-box .message-info .username{
    display: inline-block;
    font-weight: 600;
    margin: 0px;
  
    margin-top: -3px;
    float: left;
    
}

.chat-user-box .message-info .lastdate{
    display: inline-block;
    margin: 0px;
    margin-top: -3px;
    color: gray;
    font-size: 10px;

    
}
.chat-user-box .message-info .inner{
    overflow: hidden;
}
.chat-user-box .message-info .lastmessage{
    display: block;
    margin-top: -4px;
    max-height: 2em; /* (Number of lines you want visible) * (line-height) */
    line-height: 2em;
    overflow: hidden;
    text-overflow: ellipsis;

    
  
}

.close-offer {
    position: absolute;
    top: 0;
    right:0;
    margin-right: 10px;
    margin-top: 10px;
    /* padding: 5px 10px; */
    font-weight: 600;
    background-color: transparent;
    border: none;
    font-size: 26px;
}